<template>
  <div class="container-rework-history">
    <div class="search-form">
      <a-form :form="form" layout="vertical">
        <a-row :gutter="24">
          <a-col :span="4">
            <a-form-item label="批次号">
              <a-input placeholder="请输入批次号" v-model="form.containerId"></a-input>
            </a-form-item>
          </a-col>
          <a-col :span="4">
            <a-form-item label="操作员">
              <a-input placeholder="请输入操作员" v-model="form.operator"></a-input>
            </a-form-item>
          </a-col>
          <a-col :span="4">
            <a-form-item :label="'\u00a0'">
              <g-btn text="查询" class="mr-2" type="primary"></g-btn>
              <g-btn text="重置"></g-btn>
            </a-form-item>
          </a-col>
        </a-row>
      </a-form>
    </div>
    <div class="table-container">
      <g-table-bar :spacing.sync="tableSize" :columns.sync="columns" :virtualColumns="virtualColumns" @refreshTable="refresh">
        <g-btn text="导出excel" style="margin-right: 10px"></g-btn>
      </g-table-bar>
      <a-table
        id="rework-history-table"
        :columns="columns"
        :data-source="dataList"
        :scroll="{ y: tableheight }"
        :row-key="(record) => record.id"
        :pagination="pagination"
        :loading="isTableLoading"
        :size="tableSize"
      >
      </a-table>
    </div>
  </div>
</template>

<script>
import { getTableScroll } from "@/utils/getTableScroll";
import { tableBase } from "@/utils";

export default {
  name: "container-rework-history",
  mixins: [tableBase],
  data() {
    return {
      tableheight: 0,
      form: {
        containerId: "",
        operator: "",
      },
      columns: [
        {
          title: "序号",
          dataIndex: "id",
          key: "id",
          customRender: (text, record, index) => `${index + 1}`,
          width: 80,
        },
        {
          title: "批次号",
          dataIndex: "containerId",
          key: "containerId",
        },
        {
          title: "返工原因",
          dataIndex: "reworkReason",
          key: "reworkReason",
          ellipsis: true,
        },
        {
          title: "返工时间",
          dataIndex: "reworkTime",
          key: "reworkTime",
          sorter: true,
          width: 200,
        },
        {
          title: "操作员",
          dataIndex: "operator",
          key: "operator",
        },
        {
          title: "备注",
          dataIndex: "remark",
          key: "remark",
          ellipsis: true,
        },
      ],
    };
  },
  mounted() {
    this.virtualColumns = JSON.parse(JSON.stringify(this.columns)).map(
      (item) => {
        item.checked = true;
        return item;
      }
    );
    this.$nextTick(() => {
      this.tableheight = getTableScroll({ id: "rework-history-table" });
    });
  },
  methods: {
    fetchData(request) {
      // 模拟数据
      this.isTableLoading = true;
      setTimeout(() => {
        var _data = [];
        for (let i = 0; i < 50; i++) {
          _data[i] = {
            id: i,
            containerId: "批次-" + i,
            reworkReason: "原因-" + i,
            reworkTime: "2023-03-21 10:00:00",
            operator: "操作员-" + i,
            remark: "备注-" + i,
          };
        }
        request.finishedCallback && request.finishedCallback();
        request.successCallback &&
          request.successCallback({ items: _data, totalCount: 50 });
      }, 200);
    },
  },
};
</script>

<style lang="scss" scoped>
.container-rework-history {
  .search-form {
    background-color: #fff;
    margin-bottom: 10px;
    padding: 12px 24px;
    box-sizing: border-box;
    border-radius: 4px;
  }
  .table-container {
    background-color: #fff;
    padding: 12px 24px 0;
    box-sizing: border-box;
    overflow-y: hidden;
    border-radius: 4px;
  }
}
</style>